.AttractiveMessage_article_button {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-around;
    flex: 1;
}

.AttractiveMessage_button {
    font-weight: 100;
    font-size: 18px;
    border: none;
    background-color: #6400E3;
    color: #ffff;
    width: calc(50% - 70px);
    height: 40px;
    margin: 35px;
    margin-top: 15px;
}

.AttractiveMessage_button_previous[disabled] {
    cursor: not-allowed;
    opacity: 0.5;
}

.AttractiveMessage_button_next {
    background-color: #ffff;
    border: none;
    border-radius: 30px;
    color: #6400E3;
}

.message1_title {
    color: white;
    font-size: 25px;
    text-align: center;
    margin: 0px;
    padding: 0px;
    margin-left: 25px;
    margin-right: 25px;
    margin-top: 15px;
    margin-bottom: 15px;
}

.message1_subtile {
    color: white;
    font-size: 15px;
    text-align: center;
    margin-left: 25px;
    margin-right: 25px;
    font-weight: 100;
}

.message2_subtile {
    color: white;
    font-size: 25px;
    text-align: center;
    margin-left: 25px;
    margin-right: 25px;
    font-weight: 100;
}

.message1_subtile_strong {
    text-decoration: underline;
    color: #F5606E;
    font-weight: bold;
}

.message1_svg {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: calc(100% - 80px);
    padding-left: 40px;
    padding-right: 40px;
}

.message2_svg {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: calc(100% - 164px);
    padding-left: 82px;
    padding-right: 82px;
    position: relative;
    overflow: hidden;
}

.message3_svg {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: calc(100% - 80px);
    padding-left: 40px;
    padding-right: 40px;
    position: relative;
    overflow: hidden;
    margin-top: 6px;
}

.template_smartphone {
    transition: transform 0.5s ease-in-out;
}

.arrow_animation_01 {
    animation: arrow_animation1 0.7s linear 0.7s infinite;
    opacity: 1;
}

@keyframes arrow_animation1 {
    0%, 33%{
        opacity: 1;
    }
    33.1%, 66% {
        opacity: 0;
    }
    66.1%, 100% {
        opacity: 0;
    }
}

.arrow_animation_03 {
    animation: arrow_animation3 0.7s linear 0.7s infinite;
    opacity: 0;
}

@keyframes arrow_animation3 {
    0%, 33%{
        opacity: 0;
    }
    33.1%, 66% {
        opacity: 1;
    }
    66.1%, 100% {
        opacity: 0;
    }
}

