.message3_ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    margin-top: 15px;
    margin-left: 25px;
    line-height: 2;
}

.message3_li {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.message3_number {
    width: 30px;
    height: 30px;
    border-radius: 50px;
    background-color: white;
    color: #6400E3;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    font-weight: 100;
}

.message3_text {
    color: white;
    margin-left: 15px;
    font-size: 20px;
    font-weight: 100;
}

.message3_text_strong {
    font-weight: bold;
    text-decoration: underline;
    color: #F5606E;
}

.loading-screen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #6200e3;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
    animation: fade-inX 0.5s ease-out, fade-outX 0.5s ease-in 0.5s;
}

.loading-icon {
    color: white;
    width: 80px;
    height: 80px;
    text-align: center;
}

@keyframes fade-in {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fade-out {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

